.about {
  padding-top: 125px;
  padding-bottom: 20px;
  background-color: var(--secondary-background-color);
  background-size: 100%;

  @media screen and (max-width: $pre-tablet) {
    background-image: url(../images/about/about-bg-mobile.png);
    background-repeat: no-repeat;
    background-position: bottom;

    // ====== для Retina экранов:
    @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
      background-image: url(../images/about/about-bg-mobile@2x.png);
    }
  }

  // ======для десктопа
  @media screen and (min-width: $desktop) {
    background-image: url(../images/about/about-bg-desktop.png);
    background-repeat: no-repeat;
    background-position: bottom;
  }

  @media screen and (min-width: $tablet) {
    padding-top: 130px;
  }
}
.about__title {
  margin-bottom: 25px;

  @media screen and (min-width: $tablet) {
    margin-bottom: 50px;
  }
}

// =============контейнер с картинкой
.about__box {
  @media screen and (min-width: $tablet) {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}
.about__thumb {
  @media screen and (max-width: $pre-tablet) {
    margin-bottom: 45px;
  }
}
.about__picture {
  @media screen and (min-width: $tablet) {
    max-width: 340px;
  }
  // & img{
  //   max-width: none;
  // }
}

// ===========контейнер с текстом
.about__text-box {
  padding: 0 30px;

  @media screen and (min-width: $tablet) {
    padding: 0;
    width: 340px;
  }
  @media screen and (min-width: $desktop) {
    padding-left: 70px;
    width: 463px;
  }
}
.about__text {
  margin-bottom: 40px;

  font-weight: 500;
  font-size: 12px;
  line-height: 1.62;

  &:nth-child(2) {
    margin-bottom: 15px;
  }
  &:nth-child(3) {
    margin-bottom: 25px;
  }

  @media screen and (min-width: $tablet) {
    font-size: 14px;
    line-height: 1.76;
  }
  @media screen and (min-width: $desktop) {
    font-size: 16px;
  }
}
.about__text--light {
  color: var(--description-color);
}

// ==============кнопка
.about__btn {
  display: flex;
  align-items: center;

  width: 176px;
  height: 44px;
  padding: 10px 22px;
  border-radius: 22px;

  color: var(--accent-color);
  background-color: var(--white-color);

  font-family: inherit;
  font-weight: 700;
  font-size: 16px;
  line-height: 1.31;

  transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);

  &:hover,
  &:focus {
    background-color: #ecebeb;
  }
}

.about__btn-icon {
  margin-left: 14px;
  width: 8px;
  height: 10px;
}

// ========================МОДАЛКА

.about-modal {
  //это модалка показана
  position: absolute;
  top: 50%;
  left: 50%;
  padding: 40px;
  // при экране 320рх модалка на весь экран
  width: 100vw;
  background-color: var(--secondary-background-color);
  transform: translate(-50%, -50%) scale(1);
  transition: transorm 250ms cubic-bezier(0.4, 0, 0.2, 1);
  overflow: auto;
  z-index: 9;

  //  когда бекдроп спрятан, мы чтото применяем к .about-modal
  .about-backdrop.is-hidden & {
    transform: translate(-50%, -50%) scale(0.9);
  }
}
.about-modal .about__text-box {
  width: auto;
}

//===============кнопка КРЕСТИК закрытия модального окна
.modal__btn-close {
  position: absolute;
  top: 0;
  right: 0;
  transform: translate(-8px, 8px);
  display: flex;
  align-items: center;
  justify-content: center;
  width: 30px;
  height: 30px;
  padding: 0;

  background-color: var(--white-color);
  border: 4px solid rgba(0, 0, 0, 0.1);
  border-radius: 50%;
}
.modal__btn-close-icon {
  align-items: baseline;
  width: 12px;
  height: 12px;
  transition: fill 250ms cubic-bezier(0.4, 0, 0.2, 1);

  &:hover {
    fill: var(--accent-color);
  }
}

//===============кнопка НИЖНЯЯ закрытия модального окна
.modal__btn {
  display: block;
  margin-left: auto;
  margin-right: auto;
  color: var(--white-color);
  background-color: var(--accent-color);
  transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1),
    box-shadow 250ms cubic-bezier(0.4, 0, 0.2, 1), transform 250ms cubic-bezier(0.4, 0, 0.2, 1);

  &:hover,
  &:focus {
    background-color: var(--accent-color);
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.5);
    transform: scale(1.2);
  }
}
// конец